<template>
	<view class="boxs">
		<view class="label" v-if="name === 'activity'" :style="{background: order.Type===0?'#00D08F':'#FEB042'}">
			{{order.Type===0?'免费': '付费'}}
		</view>
		<view class="activity">
			<view class="image">
				<image :src="`https://farm.juzhentech.com${order.Image}`" mode=""></image>
			</view>
			<view class="image-right">
				<view class="one">
					<text
						:style="{background:order.Status===0?'rgba(0, 208, 143, 0.15)':'rgba(254, 176, 66, 0.15)',border: order.Status===0?'2rpx solid #00D08F': '2rpx solid #FEB042',color: order.Status===0?'#00D08F':'#FEB042'}">{{order.Status===0?'报名中':'已结束'}}</text>
					<text>{{order.Title}}</text>
				</view>
				<!-- <view class="two">手比赛场</view> -->
				<view class="threeall">
					<view class="three">
						<image src="@/static/img/icon_date@3x.png" mode=""></image>
						<!-- <uni-icons type="wallet-filled" color="#00D08F"></uni-icons> -->
						<text class="text">{{order.ActivityTime}}</text>
					</view>
					<view class="three">
						<image src="@/static/img/icon_location@3x.png" mode=""></image>
						<!-- <uni-icons type="heart" color="#00D08F"></uni-icons> -->
						<text class="text">{{order.ActivityAddress}}</text>
					</view>
					<view class="three">
						<image src="@/static/img/icon_principal@3x.png" mode=""></image>
						<!-- <uni-icons type="person" color="#00D08F"></uni-icons> -->
						<text class="text">{{order.NickName}}</text>
					</view>
				</view>
			</view>
		</view>
		<view class="bottom">
			<view class="bottom-left">
				<view class="imgs">
					<image v-for="(item,index) in order.SignList" :key="index"
						:style="[{'margin-left': (index*25)+'rpx'}]" :src="`https://farm.juzhentech.com${item.Image}`"
						mode=""></image>
				</view>
			</view>
			<view class="bottom-right">
				<view class="text">{{order.SignCount}}人已上车</view>
				<view class="">
					<image v-if="name === 'collection'" src="@/static/img/icon_like@3x_1.png" @click="onCollection"
						mode=""></image>
				</view>
				<view class="btn" @click="handleEvent">立即上车</view>
			</view>
		</view>

	</view>
</template>

<script>
	import falseCollection from '@/static/img/icon_like@3x.png'
	import trueCollection from '@/static/img/icon_like@3x_1.png'
	import {
		domain
	} from '@/config/index'
	export default {
		props: {
			name: {
				type: String,
				default: ''
			},
			order: {
				default: {},
				trueCollection,
				falseCollection
			}
		},
		data() {
			return {
				domain,
			}
		},
		methods: {
			handleEvent() {
				uni.navigateTo({
					url: `/pages/activity/event-details/index?activityId=${this.order.ActivityID}`
				})

			},
			/**
			 * 取消收藏
			 */
			onCollection() {
				this.$emit('onCollection', this.order.CollectionID)
			},
		}
	}
</script>

<style scoped lang="scss">
	.boxs {
		background: linear-gradient(180deg, #EEFFF6 0%, #FFFFFF 100%);
		box-shadow: 0px 4rpx 20rpx 0rpx rgba(0, 100, 69, 0.07);
		border-radius: 20rpx;
		padding: 30rpx;
		margin-bottom: 30rpx;
		position: relative;
	}

	.label {
		top: 50rpx;
		left: 0rpx;
		position: absolute;
		z-index: 20;
		width: 88rpx;
		height: 44rpx;
		background: #FEB042;
		box-shadow: 4rpx 0rpx 8rpx 0rpx rgba(0, 0, 0, 0.1);
		border-radius: 0px 10rpx 10rpx 0rpx;
		line-height: 44rpx;
		font-size: 24rpx;
		font-weight: 500;
		color: #FFFFFF;
		text-align: center;
		text-shadow: 4rpx 0rpx 8rpx rgba(0, 0, 0, 0.1);
	}

	.activity {
		display: flex;
		justify-content: start;
		padding-bottom: 30rpx;
		border-bottom: 2rpx solid #EFEFEF;

		.image {
			width: 240rpx;
			height: 240rpx;

			image {
				width: 240rpx;
				height: 240rpx;
				border-radius: 20rpx;
			}
		}

		.image-right {
			padding-left: 20rpx;

			.one {
				text:nth-of-type(1) {
					background: rgba(0, 208, 143, 0.15);
					border-radius: 4rpx;
					border: 2rpx solid #00D08F;
					padding: 2rpx 6rpx;
					font-size: 20rpx;
					font-weight: 500;
					color: #00D08F;
					line-height: 28rpx;
					margin-right: 10rpx;
				}

				view:nth-of-type(2) {
					padding-left: 10rpx;
					font-size: 32rpx;
					font-weight: 500;
					color: #333333;
					line-height: 44rpx;
					letter-spacing: 1rpx;
				}
			}

			.two {
				font-size: 32rpx;
				font-weight: 500;
				color: #333333;
				line-height: 44rpx;
				letter-spacing: 1rpx;
				padding: 10rpx 0 24rpx;
			}

			.threeall {
				padding-top: 24rpx;
			}

			.three {
				font-size: 24rpx;
				font-weight: 400;
				color: #333333;
				line-height: 32rpx;
				display: flex;
				justify-content: start;

				// align-items: center;
				// &:nth-of-type(2) {
				// 	padding: 30rpx 0;
				// }
				.text {
					flex: 1;
					padding-left: 10rpx;
				}

				image {
					width: 35rpx;
					height: 35rpx;
				}
			}

			.three:nth-of-type(2) {
				padding: 16rpx 0;
			}

			.threes {
				display: flex;
				justify-content: start;
				padding: 8rpx 0;

			}
		}

		// image {
		// 	width: 240rpx;
		// 	height: 240rpx;
		// 	border-radius: 20rpx;
		// }
	}

	.bottom {
		padding-top: 30rpx;
		display: flex;
		justify-content: space-between;


		.bottom-left {
			.imgs {
				position: relative;
			}

			image {
				top: 14rpx;
				position: absolute;
				width: 40rpx;
				height: 40rpx;
				border-radius: 50%;
				border: 4rpx solid #fff;

			}
		}

		.bottom-right {
			display: flex;
			justify-content: space-between;
			align-items: center;

			image {
				margin-left: 20rpx;
				width: 60rpx;
				height: 60rpx;
			}

			.text {
				font-size: 28rpx;
				font-weight: 400;
				color: #333333;
				line-height: 40rpx;
			}
		}

		.btn {
			margin-left: 20rpx;
			text-align: center;
			width: 244rpx;
			height: 80rpx;
			background: #00D08F;
			border-radius: 52rpx;
			backdrop-filter: blur(20rpx);
			font-size: 28rpx;
			font-weight: 500;
			color: #FFFFFF;
			line-height: 80rpx;
		}
	}
</style>
